
const reportSlide = document.querySelectorAll('.report-slide');
const fadeOut = (el, timeout) => {
  el.style.opacity = 1;
  el.style.transition = `opacity ${timeout}ms`;
  el.style.opacity = 0;

  setTimeout(() => {
    el.style.display = 'none';
  }, timeout);
};

const fadeIn = (el, timeout, display) => {
  el.style.opacity = 0;
  el.style.display = display || 'block';
  el.style.transition = `opacity ${timeout}ms`;
  setTimeout(() => {
    el.style.opacity = 1;
  }, 10);
};

if (reportSlide) {
  window.onload = function () {
    let stopVideos = (ind) => {
      reportSlide.forEach((elem, index) => {
        let buttonPlay = elem.querySelector('.report__play');
        let video = elem.querySelector('video');
        video.pause();
        video.currentTime = 0;
        video.removeAttribute('controls');
        if (ind != index) {
          fadeIn(buttonPlay, 300)
        }
        elem.classList.remove('--is-active');
      })
    };
    reportSlide.forEach((elem, index) => {
      let buttonPlay = elem.querySelector('.report__play');
      let video = elem.querySelector('.report__video video');


      buttonPlay.addEventListener('click', () => {
        stopVideos(index);
        video.play();
        fadeOut(buttonPlay, 300);
        video.setAttribute('controls', true);
        video.muted = false;
        elem.classList.add('--is-active');
      });


      elem.addEventListener('mouseover', () => {
        video.muted = true;
        video.play();
      })
      elem.addEventListener('mouseout', () => {
        video.muted = true;
        video.pause();
        video.currentTime = 0;
      })
    })
  }
}

